import React, { useContext } from "react";
import classNames from 'classnames';
import './MovieCard.scss';
import { AppContext } from '../../App'

export const MovieCard = ({ movie }) => {

  const {
    id,
    original_title,
    poster_path,
    vote_average
  } = movie;


  const { handleChosenMovieId } = useContext(AppContext)

  return (

    <li
      disabled={!!poster_path}
      className={classNames(
        'movie-card',
        {'movie-card--is-disabled': !poster_path}
      )}

      onClick={() => {
        handleChosenMovieId(id)
      }}

    >
      <span className="movie-card__ranking">IMDB: {vote_average}</span>
      <img
        className={classNames(
          'movie-card__img',
        )}
        src={`https://image.tmdb.org/t/p/w300/${poster_path}`} alt={original_title}

      />
    </li >

  )
}

