import classNames from "classnames";
import React, { useState, useEffect } from "react";
import { MovieCard } from "../MovieCard";
import './Cart.scss';


export const Cart = ({ setOpenCart, movies, setCart }) => {

  const [showContent, setShowContent] = useState(false)

  useEffect(() => {
    setShowContent(true)
    document.body.style.overflow = 'hidden';
    return () => document.body.style.overflow = 'unset';
  }, [])

  const handleShowContent = (event) => {
    if (event.target !== event.currentTarget) return;
    setShowContent(false)

    setTimeout(() => {
      setOpenCart(false)
    }, 1000);
  }

  console.log();

  return (
    <div
      className="cart"
      onClick={handleShowContent}
      
    >
      <div className={classNames(
        'cart__content',
        {
          'cart__content--is-open': showContent,
          'cart__include-product': !!movies.length
        }
      )}
      // style={{backgroundImage: `url(https://image.tmdb.org/t/p/w780${movies[0].backdrop_path})`}}

      >
        <div
          className="cart__close-cross"
          onClick={handleShowContent}
        >
          X
        </div>

        <div className="cart__title">Movies</div>
        <div
          className="cart__movies"
          >

          {
            movies.map(movie => (
              <div className="cart__movie">
                <span
                  className="cart__movie--remove"
                  onClick={() => {
                    const filtered = movies.filter(obj => obj.id !== movie.id)
                    setCart(filtered)
                  }}
                >
                  remove
                </span>
                < MovieCard movie={movie} />

              </div>
            ))
          }
        </div>
        <div className="cart__client-field">123</div>


      </div>

    </div>
  )
}
