export const Carousel = React.memo(({ items, currentTag }) => {


  const [marginLeft, setMarginLeft] = useState(0)
  const [carouselWidth, setCarouselWidth] = useState(0)

  const { handleUserChoise } = useContext(PopupContentContext)

  const handleMargin = (event) => {
    const { value } = event.target;

    if (marginLeft + +value) {
      setMarginLeft()
    }
    const totalLength = step * items.length;
    const marginLimit = totalLength - step * 4
    const margin = +value + marginLeft
    setMarginLeft(marginLeft + +value)
  }


  // const test = useCallback(debaunce(setCarouselWidth, 1000))

  // useEffect(() => {

  //   test()
  // })

  return (
    <div className="carousel">
      <button
        className="carousel__button carousel__button--prev"
        value={-step}
        hidden={items.length * step <= carouselWidth}
        // disabled={test()}
        onClick={handleMargin}
      >
        {'<'}
      </button>
      <div className="carousel__box-items">
        <ul
          className="carousel__items"
          style={{ marginLeft }}
        >
          {
            !!items && items.map(item => (
              <li
                className="carousel__item"
                onClick={() => handleUserChoise({ id: item.id, tag: currentTag })}
                key={item.id}
              >
                <img src={`https://image.tmdb.org/t/p/w200/${item.profile_path || item.poster_path}`} alt={item.title || item.title} />
                <h3 >{item.name || ''}</h3>
              </li>
            ))
          }
        </ul>
      </div>
      <button
        className="carousel__button carousel__button--next"
        // disabled={}
        value={step}
        onClick={handleMargin}
      >
        {'>'}
      </button>
    </div>
  )
})
